.tableTitle {
    background: #17172e;
    color: white;
}
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 5ch;
    margin-left: 220px;
}
.container table {
    flex-shrink: 3;
}

.tableTitle th, table td {
    padding: 1ch;
}

.content {
   display: none;
}

h2 {
    text-align: center;
}

.closeBtn {
    border: 1px solid black;
    padding: 1ch 1.5ch;
    border-radius: 50%;
    cursor: pointer;
    margin-left: 95%;
}
.closeBtn:hover {
    color: white;
    background: black;
}

