.sidenav .content {
    display: flex;
    flex-wrap: wrap;
    flex-shrink: 3;
}

.sidenav button {
    background: none;
    border: none;
    cursor: pointer;
    padding: 2ch;
    text-transform: uppercase;
    color: white;
    width: 100%;
}

.sidenav button:hover {
    background-color: #31334a;
    color: white;
}

.sidenav {
    height: 100%;
    width: 220px;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background: #17172e;
    overflow-x: hidden;
    padding-top: 20px;
}

.sidenav button, .dropdown-btn {
    display: block;
    text-align: left;
    outline: none;
}
  
.dropdown-container {
  display: none;
  width: 100%;
}

.active {
    background: #363649!important;
    color: white;
}

.dropdown-container button {
    background-color: #585a71;
    color: white;
}